.org_sec {
    padding: 7 / $setFs + rem 0;
    border-top: 4 / $setFs + rem solid #f8f8f8;
    border-bottom: 4 / $setFs + rem solid #f8f8f8;
    position: relative;
}

.swiper_org {
    .swiper-slide {
        flex: 0;
        padding: 2 / $setFs + rem 38 / $setFs + rem;
        box-sizing: border-box;

        @media screen and (max-width: 768px) {
            padding: 2 / $setFs + rem 25 / $setFs + rem;
        }
    }
}

.org_link {
    @include acc;
}

.org_logo {
    width: auto;
    height: 67 / $setFs + rem;

    @media screen and (max-width: 768px) {
        height: 50 / $setFs + rem;
    }
}

.sitemap_sec {
    padding-top: 50 / $setFs + rem;
    padding-bottom: 34 / $setFs + rem;
}

.sitemap_row {
    display: flex;
}

.sitemap_left {
    flex: 0 0 395 / $setFs + rem;
    padding-right: 105 / $setFs + rem;
    box-sizing: border-box;
}

.sitemap_right {
    flex: 1;

    @media screen and (max-width: 768px) {
        display: none;
    }
}

.sitemap_info {
    margin-bottom: 55 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-bottom: 34 / $setFs + rem;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.sitemap_info_title {
    font-size: 18 / $setFs + rem;
    font-family: $font2;
    font-weight: 500;
    padding-bottom: 7 / $setFs + rem;
    border-bottom: 1 / $setFs + rem solid #f0f0f0;
    position: relative;
    margin-bottom: 12 / $setFs + rem;

    &:after {
        content: "";
        width: 114 / $setFs + rem;
        height: 1 / $setFs + rem;
        background: $txtColor;
        position: absolute;
        bottom: -1 / $setFs + rem;
        left: 0;
    }

    .fa-clock {
        font-size: 19 / $setFs + rem;
        position: relative;
        bottom: -1 / $setFs + rem;
    }

    .fa-map-marker-alt {
        font-size: 23 / $setFs + rem;
    }
}

.sitemap_info_txt {
    letter-spacing: 0;
}

.sitemap_info_link {
    display: inline;
    @include acc;
}

.sitemap_list {
    display: flex;
    justify-content: space-between;
}

.sitemap_link {
    font-size: 18 / $setFs + rem;
    font-family: $font2;
    font-weight: 600;
    @include acc;
}

.sitemap_sub_list {
    margin-top: 5 / $setFs + rem;
}

.sitemap_sub_link {
    font-size: 1rem;
    color: #999999;
    line-height: 2;
    @include acc;
}

.footer {
    color: #fff;
    background: #333;
    padding: 30 / $setFs + rem 0;
    border-top: 1 / $setFs + rem solid #a0a0a0;

    .container {
        max-width: none;
    }
}

.footer_row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    @media screen and (max-width: 768px) {
        display: block;
    }

    &:first-child {
        margin-bottom: 14 / $setFs + rem;

        @media screen and (max-width: 768px) {
            margin-bottom: 30 / $setFs + rem;
        }
    }
}

.footer_left {
    @media screen and (max-width: 768px) {
        margin-bottom: 30 / $setFs + rem;
    }
}

.footer_info_list {
    display: flex;

    @media screen and (max-width: 768px) {
        display: block;
    }
}

.footer_info_li {
    font-size: 14 / $setFs + rem;
    margin-right: 18 / $setFs + rem;

    @media screen and (max-width: 768px) {
        line-height: 2;
    }
}

.footer_info_title {
    position: relative;

    &::after {
        content: "/";
        color: #666;
    }
}

.footer_link_list {
    display: flex;

    .fa-facebook-f {
        font-size: 18 / $setFs + rem;
    }
}

.footer_link_li {
    margin-right: 40 / $setFs + rem;
}

.footer_link_link {
    font-size: 15 / $setFs + rem;
    @include acc;
}

.footer_logo_list {
    display: flex;
    justify-content: flex-end;
    margin-left: -30 / $setFs + rem;
    margin-right: -30 / $setFs + rem;

    @media screen and (max-width: 768px) {
        justify-content: flex-start;
    }
}

.footer_logo_li {
    padding: 0 30 / $setFs + rem;
}

.footer_logo_link {
    @include acc;
}

.logo_1 {
    flex: 0 0 131 / $setFs + rem;
}

.logo_2 {
    flex: 0 0 137 / $setFs + rem;
    position: relative;

    &::before {
        content: "";
        width: 1 / $setFs + rem;
        height: 19 / $setFs + rem;
        background: #666666;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
    }
}

.copyright {
    font-size: 14 / $setFs + rem;
    text-align: right;

    @media screen and (max-width: 768px) {
        text-align: left;
    }
}