.gallery_sec {
    background: $color2;
    padding: 60 / $setFs + rem 0 80 / $setFs + rem;
    margin-top: 50 / $setFs + rem;
}
.gallery_container {
    $x: 180 / $setFs + rem;
    width: calc(100% - #{$x});
    position: relative;
    margin: 0 auto;
    @media screen and (max-width: 768px) {
        $x: 20 / $setFs + rem;
        width: calc(100% - #{$x});
    }
    @media screen and (max-width: 480px) {
        width: 100%;
    }
}
.gallery_swiper {
    position: relative;
    margin-top: 30 / $setFs + rem;
    @media screen and (max-width: 768px) {
        width: 90vw;
    }
    .swiper-container {
        @media screen and (max-width: 768px) {
            overflow: visible;
        }
    }
    .swiper-slide {
        padding: 0 15 / $setFs + rem;
        box-sizing: border-box;
        @media screen and (max-width: 768px) {
            padding: 0 10 / $setFs + rem;
        }
    }
    .swiper-button-prev {
        left: -70 / $setFs + rem;
        @media screen and (max-width: 768px) {
            display: none;
        }
    }
    .swiper-button-next {
        right: -70 / $setFs + rem;
        @media screen and (max-width: 768px) {
            display: none;
        }
    }
}
.gallery_item {
    position: relative;
}
.gallery_img {
    height: 206 / $setFs + rem;
    object-fit: cover;
}
