.ck_editor {
    font-size: ($setFs + 1) / $setFs + rem;
    line-height: (40 / 21);
    text-align: justify;
    a {
        color: $color;
        display: inline;
        position: relative;
        &:hover {
            text-decoration: underline;
            // &::before {
            //     display: none;
            // }
        }
        // &::before {
        //     content: "";
        //     width: 100%;
        //     border-bottom: 1 / $setFs + rem dotted #000;
        //     position: absolute;
        //     bottom: -1 / $setFs + rem;
        //     left: 0;
        // }
    }
    p {
        margin-bottom: 30 / $setFs + rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    ul {
        list-style-type: disc;
        li {
            margin-left: 18 / $setFs + rem;
        }
    }
    iframe {
        height: 450 / $setFs + rem;
        margin-bottom: 30 / $setFs + rem;
    }
    img {
        max-width: 800 / $setFs + rem;
        margin: 0 auto;
        border: 10 / $setFs + rem solid #dbdbdb;
        box-sizing: border-box;
    }
    figure {
        margin-bottom: 30 / $setFs + rem;
    }
    figcaption {
        font-size: 1rem;
        color: #9e9e9e;
        text-align: center;
        position: relative;
        margin-top: 5 / $setFs + rem;
    }
}
.img-responsive {
    display: inline-block;
}
