.breadcrumb {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10 / $setFs + rem;
    margin-right: -10 / $setFs + rem;

    @media screen and (max-width: 768px) {
        margin-bottom: 20 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        margin-bottom: 30 / $setFs + rem;
    }

    &.pt {
        padding-top: 20 / $setFs + rem;

        @media screen and (max-width: 480px) {
            padding-top: 30 / $setFs + rem;
        }
    }

    &.pb {
        padding-bottom: 30 / $setFs + rem;
    }
}

.breadcrumb_li {
    display: flex;
    position: relative;

    &::before {
        content: ">";
    }

    &:first-child {
        &::before {
            display: none;
        }
    }
}

.breadcrumb_link {
    padding: 0 10 / $setFs + rem;
    @include acc;
}