$winSize: 1920;

html {
    width: 100%;
    font-family: $fontDefualt;
    color: $txtColor;
    font-size: $setFs/$winSize * 100vw;
    letter-spacing: 1 / $setFs + rem;
    position: relative;
    overflow-x: hidden;

    // @media screen and (max-width: 1440px) {
    //     $winSize: 1680;
    //     font-size: $setFs/$winSize * 100vw;
    // }

    @media screen and (max-width: 768px) {
        $winSize: 840;
        font-size: $setFs/$winSize * 100vw;
    }

    @media screen and (max-width: 480px) {
        $winSize: 460;
        font-size: $setFs/$winSize * 100vw;
    }
}

body {
    @extend html;
}

.lang_en {
    letter-spacing: 0;
}

.wrapper {
    width: 100%;
    line-height: (48 / 34);
    position: relative;
    overflow: hidden;
    z-index: 1;
    padding-top: 85 / $setFs + rem;

    @media screen and (max-width: 768px) {
        padding-top: 68 / $setFs + rem;
    }
}

.container {
    $x: 80 / $setFs + rem;
    width: calc(100% - #{$x});
    max-width: 1180 / $setFs + rem;
    position: relative;
    margin: 0 auto;
    z-index: 1;

    @media screen and (max-width: 768px) {
        $x: 40 / $setFs + rem;
        width: calc(100% - #{$x});
        max-width: 1000 / $setFs + rem;
    }

    @media screen and (max-width: 480px) {
        $x: 20 / $setFs + rem;
        width: calc(100% - #{$x});
        max-width: 1000 / $setFs + rem;
    }
}

section {
    position: relative;
}