.hashtag_list {
    margin-top: 50 / $setFs + rem;
    display: flex;
    flex-wrap: wrap;
}
.hashtag_li {
    margin-right: 20 / $setFs + rem;
    margin-bottom: 10 / $setFs + rem;
}
.hashtag_link {
    font-size: ($setFs + 1) / $setFs + rem;
    font-weight: bold;
    line-height: (26/16);
    @include acc;
    &:before {
        content: "#";
        margin-right: 3 / $setFs + rem;
    }
    &:hover {
        color: $color;
    }
}
