@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+TC:wght@200;300;400;500;600;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=PT+Serif&display=swap');



$color: #dd472f;
$color2: #f8f8f8;
$setFs: 16;
$txtColor: #333;
$fontDefualt: "微軟正黑體",
"蘋果儷黑體",
Arial,
sans-serif,
Verdana;
$font1: 'PT Serif',
"Noto Sans TC",
$fontDefualt;
$font2: 'PT Serif',
"Noto Serif TC",
$fontDefualt;

:focus,
button:focus {
    outline: 0;
}

svg {
    display: block;
    width: 100%;
    height: 100%;
}

a {
    display: block;
    text-decoration: none;
    transition: all 0.2s ease-out;
    color: inherit;
    box-sizing: border-box;
    position: relative;

    &:hover {
        color: inherit;
        text-decoration: none;
    }
}

@mixin word_limit($line: 2) {
    display: -webkit-box;
    -webkit-line-clamp: $line;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

@mixin _w {
    @media screen and (min-width: 769px) {
        &:after {
            border-color: #fff;
        }
    }
}

@mixin acc {
    @media screen and (min-width: 769px) {
        &:after {
            content: "";
            width: calc(100% + 4px);
            height: calc(100% + 4px);
            border: 2px dotted $color;
            border-radius: 3 / $setFs + rem;
            box-sizing: border-box;
            position: absolute;
            top: -2px;
            left: -2px;
            z-index: 5;
            opacity: 0;
            transition: opacity 0.1s ease-out;
        }

        &:focus {
            &:after {
                opacity: 1;
            }
        }
    }
}

@mixin acc_before {
    @media screen and (min-width: 769px) {
        &:before {
            content: "";
            width: calc(100% + 4px);
            height: calc(100% + 4px);
            border: 2px dotted $color;
            border-radius: 3 / $setFs + rem;
            box-sizing: border-box;
            position: absolute;
            top: -2px;
            left: -2px;
            z-index: 5;
            opacity: 0;
            transition: opacity 0.1s ease-out;
        }

        &:focus {
            &:before {
                opacity: 1;
            }
        }
    }
}

@mixin acc_w {
    @include acc;
    @include _w;
}

@mixin acc_before_w {
    @include acc_before;
    @include _w;
}

.hide {
    display: none;
}

body .pc {
    @media screen and (max-width: 768px) {
        display: none;
    }
}

body .pad {
    display: none;

    @media screen and (max-width: 768px) {
        display: block;
    }
}

body .mobile {
    display: none;

    @media screen and (max-width: 640px) {
        display: block;
    }
}

.clear {
    &:before {
        content: "";
        display: table;
    }

    &:after {
        @extend :before;
        clear: both;
    }
}

.c {
    clear: both;
}

@mixin clear {
    clear: both;
}

@mixin clearfix {
    &:before {
        content: "";
        display: table;
    }

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

img {
    width: 100%;
    height: auto;
    display: block;
}

iframe {
    width: 100%;
    height: auto;
}

table {
    width: 100%;

    th,
    td {
        vertical-align: middle;
    }
}

.table {
    width: 100%;
    display: table;
    table-layout: fixed;
}

.tr {
    display: table-row;
}

.td {
    display: table-cell;
    vertical-align: middle;
    position: relative;
}

.th {
    display: table-cell;
    vertical-align: middle;
    position: relative;
}

.inblock {
    font-size: 0;
    letter-spacing: 0;

    >* {
        display: inline-block;
        vertical-align: middle;
        letter-spacing: 1 / $setFs + rem;
    }
}

@mixin inblock($spacing: 0) {
    font-size: 0;
    letter-spacing: 0;

    >* {
        display: inline-block;
        vertical-align: top;
        letter-spacing: $spacing / $setFs + rem;
    }
}